import React from "react";
import { Link } from "react-router-dom";


const Header = () => {
  const [toggle,setToggle] = React.useState(false);
  const [burger] = React.useState("burger");
  const [burgert] = React.useState("burger toggle");
  const [navLink] = React.useState("nav-links");
  const [navLinkt] = React.useState("nav-links nav-active");
  const [Links] = React.useState("");
  const [Linkst] = React.useState("navlinkfade");


  const handleToggle=()=>{
      setToggle(!toggle);
  }
  const handleClose=()=>{
    if(toggle){
      setToggle(false);
    }
  }
   

  return (
    <React.Fragment>
      <header id="main-header">
        <nav>
          <div className="logo">
            <h4>Pro-dix</h4>
          </div>
          <ul className={toggle ? navLinkt : navLink}>
            <li className={toggle ? Linkst : Links} onClick={handleClose}>
              <Link to="/">Home</Link>
            </li>
            <li className={toggle ? Linkst : Links} onClick={handleClose}>
              <Link to="/about-us">About Us</Link>
            </li>
            <li className={toggle ? Linkst : Links} onClick={handleClose}>
              <Link to="/contact-us">Contact Us</Link>
            </li>
          </ul>
          <div className={toggle ? burgert : burger } onClick={handleToggle}>
            <div className="line1"></div>
            <div className="line2"></div>
            <div className="line3"></div>
          </div>
        </nav>
      </header>
    </React.Fragment>
  );
};

export default Header;
