import React,{Component} from 'react';
import axios from 'axios';

class ContactUs extends Component {
    constructor(props) {
        super(props);
        this.state = { 
            name:"",
            email:"",
            message:"",
            status:false,
            error:false,
         }
    }
    handleName=(e)=>{
        this.setState({ name: e.target.value });
    }
    handleEmail=(e)=>{
        this.setState({ email: e.target.value });
    }
    handleMessage=(e)=>{
        this.setState({ message: e.target.value });
    }
     handleSubmit= async (e)=>{
        e.preventDefault();

    const headers = {
        'Access-Control-Allow-Origin': '*',
    'Content-Type': 'application/json',
    };

    if(this.state.name === "" || this.state.email === "" || this.state.message === ""){
        this.setState({ error: "please fill all input field"  });
    }else{
        this.setState({ error: "" });
        await axios.post(
            'https://app.pro-dix.com/api/contact-us',
            {
                name: this.state.name,
                email: this.state.email,
                message: this.state.message,
            },
            {headers}
            ).then(response => {
                this.setState({ status:true  });
    
            })
            .catch(error => {
                console.log("Error ========>", error);
                this.setState({ error: "Email not sent due to internet or technical reason try later!" });
            }
        )

    }

    
    }
    render() { 
        return ( 
            <React.Fragment>
                <div className="showcase">
        <div className="container">
            <h2>Contact Us</h2>
            <div className="lower">
                <div className="contact" >
                    {this.state.error && <h5 className="form-error">{this.state.error}</h5>}
                    {this.state.status && <h5 className="form-success">Thanks! for contact us </h5>}
                    <input type="text" name="name" placeholder="Full Name*" required onChange={this.handleName}/>
                    <input type="email" name="email" placeholder="Email*" required onChange={this.handleEmail} />
                    <textarea name="message" placeholder="Your Message*" rows="6" required onChange={this.handleMessage} ></textarea>
                    <input type="button" value="Submit" onClick={this.handleSubmit} className="contact-btn"/>
                </div>
            </div>
        </div>
    </div>
            </React.Fragment>
         );
    }
}
 
export default ContactUs;