import React, { Component } from "react";
import filterObjectArray from "filter-object-array";

class Home extends Component {
  constructor(props) {
    super(props);
    this.state = {
      Data: [],
      Search: "",
      Searched: false,
      Random: false,
      Msg: "Loading...",
      s: false,
      Found: false,
      Suggest: false,
    };
    this.handleChange = this.handleChange.bind(this);
    this.handleSearch = this.handleSearch.bind(this);
  }

  componentDidMount() {
    const min = 1;
    const max = 1000;
    const rand = min + Math.random() * (max - min);
    const index = ~~rand;
    const apiUrl = "https://app.pro-dix.com/api/dictionary";
    fetch(apiUrl)
      .then((response) => response.json())
      .then((data) => {
        this.setState({ Data: data });
        this.setState({ Searched: data[index] });
        this.setState({ Msg: false });
      });
  }
  handleChange = (e) => {
    const val = e.target.value;
    this.setState({ Search: val });
  };

  handleSearch = async () => {
    const searchKey = this.state.Search.toLowerCase();
    const filtersMatchType = {
      title: searchKey,
    };
    const DataEn = await filterObjectArray({
      array: this.state.Data,
      objFilter: filtersMatchType,
    });
    // this.setState({
    //   Searched: await filterObjectArray({
    //     array: this.state.Data,
    //     objFilter: filtersMatchType,
    //   }),
    // });

    if (DataEn.length) {
      this.setState({ Searched: DataEn });
      this.setState({ Searched: this.state.Searched[0] });
      this.setState({ Found: true });
    } else {
      const filtersMatchType = {
        spanish: searchKey,
      };
      const DataFn = await filterObjectArray({
        array: this.state.Data,
        objFilter: filtersMatchType,
      });
      if (DataFn.length) {
        this.setState({ Searched: DataFn });
        this.setState({ Searched: this.state.Searched[0] });
        this.setState({ Found: true });
      } else {
        this.setState({ Searched: false });
        this.setState({ Found: true });
        const ApiUrl = `https://app.pro-dix.com/api/search/${this.state.Search}`;
        fetch(ApiUrl)
          .then((response) => response.json())
          .then((data) => {
            this.setState({ Suggest: data });
            // this.setState({ Data: data });
            // this.setState({ Searched: data[index] });
            // this.setState({ Msg: false });
          });
      }
    }
  };
  handleRandom = async () => {
    const min = 1;
    const max = 7000;
    const rand = min + Math.random() * (max - min);
    const index = ~~rand;
    const filtersMatchType = {
      id: index.toString(),
    };
    this.setState({
      Searched: await filterObjectArray({
        array: this.state.Data,
        objFilter: filtersMatchType,
      }),
    });
    this.setState({ Suggest: false });
    this.setState({ Searched: this.state.Searched[0] });
  };
  handleNext = async () => {
    let id = Number(this.state.Searched.id);
    id = id + 1;
    if (!this.state.Suggest.length) {
      const filtersMatchType = {
        id: id.toString(),
      };
      this.setState({
        Searched: await filterObjectArray({
          array: this.state.Data,
          objFilter: filtersMatchType,
        }),
      });
      this.setState({ Searched: this.state.Searched[0] });
    }
  };
  handlePrev = async () => {
    let id = Number(this.state.Searched.id);
    id = id - 1;
    if (!this.state.Suggest.length) {
      const filtersMatchType = {
        id: id.toString(),
      };
      this.setState({
        Searched: await filterObjectArray({
          array: this.state.Data,
          objFilter: filtersMatchType,
        }),
      });
      this.setState({ Searched: this.state.Searched[0] });
    }
  };

  handleSuggest = async (e) => {
    const targetId = e.target.accessKey;
    const filtersMatchType = {
      id: targetId,
    };
    const DataSg = await filterObjectArray({
      array: this.state.Data,
      objFilter: filtersMatchType,
    });
    this.setState({ Suggest: false });
    this.setState({ Searched: DataSg });
    this.setState({ Searched: this.state.Searched[0] });
  };

  playAudio() {
    const audioEl = document.getElementsByClassName("audio-element")[0];
    audioEl.play();
  }
  
  render() {
    return (
      <React.Fragment>
        <div className="showcase">
          <div className="container">
            <div className="dix">
              <input
                name=""
                type="text"
                placeholder="Search"
                onChange={this.handleChange}
              />
              <input
                name=""
                type="submit"
                value="Search"
                onClick={this.handleSearch}
              />
            </div>
            <div className="lower">
              <div className="word-list">
                <ul>
                  {this.state.Msg && <span>{this.state.Msg}</span>}
                  {!this.state.Searched && (
                    <span>
                      {this.state.Found && (
                        <>
                          <strong>{this.state.Search}:</strong> not Found !
                        </>
                      )}{" "}
                    </span>
                  )}
                  {this.state.Searched && (
                    <>
                      <li className="eng">
                        {this.state.Searched.title}{" "}
                        <i
                          className="fas fa-volume-down"
                          onClick={this.playAudio}
                        ></i>
                      </li>
                      {this.state.Searched.audio && (
                        <audio className="audio-element">
                          <source
                            src={`https://app.pro-dix.com/audio/${this.state.Searched.audio}`}
                          ></source>
                        </audio>
                      )}

                      <li className="spani">
                        <span>{this.state.Searched.spanish}</span>
                      </li>
                      <li className="pronon">
                        <span>{this.state.Searched.prononciation}</span>
                      </li>
                    </>
                  )}
                </ul>
              </div>
              {this.state.Suggest && <h4 className="sug">Did you mean ?</h4>}
              <ul className="suggest">
                {this.state.Suggest &&
                  this.state.Suggest.map((data, key) => {
                    return (
                      <li
                        Key={key}
                        onClick={this.handleSuggest}
                        accessKey={data.id}
                      >
                        {data.title}
                      </li>
                    );
                  })}

                {/* <li>abaft</li>
                <li>abacus</li>
                <li>agree</li> */}
              </ul>
              <div className="btn-group btn-desktop">
                <button className="btn" onClick={this.handlePrev}>
                  <i className="fas fa-chevron-left"></i>Previous Word
                </button>
                <button className="btn" onClick={this.handleRandom}>
                  <i className="fas fa-infinity"></i>Random Word
                </button>
                <button className="btn" onClick={this.handleNext}>
                  Next Word<i className="fas fa-chevron-right"></i>
                </button>
              </div>
              <div className="btn-group btn-mobile">
                <button className="btn" onClick={this.handlePrev}>
                  <i className="fas fa-chevron-left"></i>
                </button>
                <button className="btn" onClick={this.handleRandom}>
                  <i className="fas fa-infinity"></i>Random Word
                </button>
                <button className="btn" onClick={this.handleNext}>
                  <i className="fas fa-chevron-right"></i>
                </button>
              </div>
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default Home;
